.leading__wrapper {
  display: flex;
}

.leading__info {
  width: 100%;
  text-align: center;

  // @include lg {
  //   text-align: left;
  //   width: 49%;
  // }
}

.leading__profession {
  color: #ebae14;
  text-transform: uppercase;
  font-size: 16px;
  margin-bottom: 10px;

  @include lg {
    font-size: 18px;
    margin-bottom: 15px;
  }
}

.leading__intro {
  max-width: 100%;
  margin: 0 0 10px 0;
  font-size: 16px;
  line-height: 24px;

  @include lg {
    font-size: 20px;
    line-height: 30px;
  }
}

.leading__photo {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -10;
  display: none;

  @include lg {
    display: block;
  }
}
